/**
 * Style description.
 * @author TheoryOfNekomata
 * @date 2017-01-28
 */

@function deep-get($map, $keys) {
  $index: str-index($keys, '.');
  $key: '';
  $value: $map;

  @while ($index != null) {
    $key: str-slice($keys, 1, $index - 1);
    $value: map-get($value, $key);
    $keys: str-slice($keys, $index + 1);
    $index: str-index($keys, '.');
  }

  @return map-get($value, $keys);
}
